/* Main app container */
.app-container {
  min-height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
}

/* Watermark styles */
.watermark {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  font-size: 25vw;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.03);
  pointer-events: none;
  z-index: -1;
  user-select: none;
  white-space: nowrap;
  font-family: Arial, sans-serif;
}

/* Ensure content stays above watermark */
.content {
  position: relative;
  z-index: 1;
  flex: 1;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .watermark {
    font-size: 40vw;
  }
}
